#page2{
  @include centered-object;
}
.cities {
  @include flex-column;
  width: 1060px;
  padding: 2rem;
  margin-bottom: 2rem;
  @media (max-width: 1200px) {
    width: 860px;
  }
  @media (max-width: 960px) {
    width: 730px;
  }
  @media (max-width: 768px) {
    width: 60%;
  }
}

.city {
  @include flex-row;
  font-size: 1rem;
  border-radius: $imgBorderRadius;
  margin-bottom: 2rem;
  @media (max-width: 768px) {
    @include flex-column;
  }

  img {
    border-radius: $imgBorderRadius 0 0 $imgBorderRadius;
    width: 25%;
    @media (max-width: 768px) {
      width: 100%;
      border-radius: $imgBorderRadius $imgBorderRadius 0 0;
    }
  }

  .hotel {
    @include flex-column;
    padding: 1rem;
    padding-left: 1.8rem;
    width: 50%;
    box-sizing: border-box;
    background-color: $thirdBackground;
    @media (max-width: 960px) {
      padding-left: 1.4rem;
    }
    @media (max-width: 768px) {
      width: 100%;
    }
    .hotel_name {
      font-size: 1.6rem;
      font-weight: bold;
      margin: 0;
      margin-bottom: 14px;
    }

    img {
      width: 80px;
      border-radius: 0;
    }

    ul {
      list-style-type: none;
      margin: 0;
      margin-top: 14px;
      padding: 0;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    }

    li {
      padding-right: 12px;
      padding-left: 0;
      padding-top: 0;
      padding-bottom: 10px;
    }
  }
}

//////////////Button styling///////////////////
.booking {
  @include centered-object;
  width: 25%;
  background-color: $secondBackground;
  border-radius: 0 $btnBorderRadius $btnBorderRadius 0;
  @media (max-width: 768px) {
    width: 100%;
    border-radius: 0 0 $btnBorderRadius $btnBorderRadius;
    padding-top: 1rem;
  }
  .price {
    font-size: 1.8rem;
  }
}
